<template>

  <section class="free-board-insert">
    <sub-layout>
    	<div>
			<div class="title">
				<h4><b>자유게시판</b></h4>
			</div>
			<form>
				<div class="row">
					<div class="col-12 subject">
						<label>제목</label>
						<input type="text" class="w-80 d-inline-block form-control" v-model="freeBoard.bbs.title">
					</div>
					<div class="col-12 content">
						<textarea v-model="freeBoard.bbs.content" class="form-control"></textarea>
<!-- 							<quill :qContent="notice.bbs.content" @childs-event="parentsMethod"></quill> -->
					</div>
<!-- 						<input type="file"  id="file2" v-on:change="uploadFile"> -->
					<div class="col-12">
						<div class="btn-box float-right">
							<div>
								<button  type="button" class="btn btn-warning" @click="save">저장</button>
								<button type="button" class="btn btn-danger" @click="cancel">취소</button>
								<router-link role="button" class="btn btn-info" to="/freeBoard">목록</router-link>
							</div>
						</div>
					</div>
				</div>
			</form>
		</div>
    </sub-layout>
  </section>

</template>

<script lang="js">
import SubLayout from '@/components/layouts/SubLayout'
  export default  {
    name: 'free-board-insert',
    props: [],
    mounted() {
		
    },
    data() {
      return {
    	  freeBoard : {bbs:{title:"", content:""}},
      }
    },
    methods: {
    	cancel : function(){
			this.router.go(-1);
		},
		save : function(){
// 			if(NoticeValidation.validation(this.notice)) return; //값 검증
			if(window.confirm("작성 하시겠습니까?")){
										
					this.$http.post(`${this.store.getters.restWebPath}/freeBoard`
						,this.freeBoard
					)
					.then(({data})=>{
						if(data.result == "success"){
							alert("작성 되었습니다.");
							this.router.push(`/freeBoard/${data.id}`);
						}else{
							alert("작성 되지 않았습니다.");
						}
					})
			}
		}
    },
    computed: {

    },
    components:{
    	SubLayout
    }
}
</script>

